  <!--商品信息项-->
  <template>
  <div class="cms-form-layout product-main-warpper" v-loading='loading' :element-loading-text="loadingTitle">
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent
    style="margin-bottom:48px;">
      <el-form-item label="售价" prop="coreModel.salePrice" ref="coreModel_salePrice">
        <el-input v-model="formData.coreModel.salePrice" maxlength="8" :disabled="formData.coreModel.hasSku"></el-input>
        <span class="gray">价格控制在0.01~99999999之间的数字，如商品设置SKU，此价格自动记录为最低SKU价格
        </span>
      </el-form-item>
      <el-form-item label="市场价" prop="coreModel.marketPrice" ref="product_name">
        <el-input v-model="formData.coreModel.marketPrice" maxlength="8" :disabled="formData.coreModel.hasSku">
        </el-input>
      </el-form-item>
      <el-form-item label="成本价" prop="coreModel.costPrice">
        <el-input v-model="formData.coreModel.costPrice" maxlength="8" :disabled="formData.coreModel.hasSku">
        </el-input>
      </el-form-item>
      <el-form-item label="库存量" prop="coreModel.inventory" ref="coreModel_inventory">
        <el-input v-model="formData.coreModel.inventory" maxlength="8" :disabled="formData.coreModel.hasSku"></el-input>
        <span class="gray">库存量控制在1~99999999之间的整数，如商品设置SKU，库存量自动计 算为所有SKU库存总和
        </span>
      </el-form-item>
      <el-form-item label="库存预警值" prop="coreModel.warningValue">
        <el-input v-model="formData.coreModel.warningValue" maxlength="8" :disabled="formData.coreModel.hasSku"></el-input>
        <span class="gray">库存预警值必须少于库存量，如商品设置SKU，预警值自动计算为所有 SKU设定的预警值总和
        </span>
      </el-form-item>
      <el-form-item label="商品规格" prop="coreModel.hasSku">
        <div>
          <el-checkbox v-model="formData.coreModel.hasSku" @change="setCoreModel">启用商品规格</el-checkbox>
          <span class="gray">启用商品规格、商品的价格、库存、以SKU信息为准</span>
        </div>
        <!-- 规格信息获取 -->
        <el-checkbox-group v-model="checkSaleStandardIds" v-if="formData.coreModel.hasSku">
          <section class="standard-group" v-for="(group,index) in saleStandardInfos " :key="group.id">
            <div class="standard-group-header">
              <div class="fl">
                <span class="standard-group-name"><i v-if="group.required" class="goods-astrick">*</i>{{group.name}}</span>
                <span class="standard-group-desc" v-if="group.valueType==4">( 该规格需要上传图片 )</span>
                 <!-- 自定义规格可以删除 -->
                <span class="el-icon-delete del" v-if="group.attrType===2" @click="deleteCustomStandard(group,index)"></span>
              </div>
              <div class="fr">
                <label class="standard-group-desc">规格值名称</label>
                <el-input style="width:110px" v-model="customStandardValue['value'+index]" maxlength="30"></el-input>
                <cms-button size='small' @click='addCustomStandard(index)' style="margin-left:10px;"
                 :disabled="!group.custom">添加</cms-button>
              </div>
            </div>
            <div class="standard-group-body">
              <!-- 判断下是图片还是复选类型或单选类型 -->
              <ul v-if="group.valueType===4" class="clearfix">
                <li v-for="(item,index2) in group.attrValueSet" :key="item.attrCode" class="type-img-items">
                  <cms-picture-zone :preview='false' :index='index2'  :params="{parentIndex:index,attrCode:item.attrCode}" class="product-img-box"
                   @change="getStandardImg">
                    <div class="product-img-warpper">
                      <img v-if="standardImageUrls[item.attrCode] && standardImageUrls[item.attrCode] !==''" :src="$cms.filterUrl(standardImageUrls[item.attrCode])" alt="">
                      <span v-else class="img-plus el-icon-plus"></span>
                    </div>
                  </cms-picture-zone>
                  <div class="img-txt">
                    <el-checkbox :label="item.attrCode" class="check-box" v-model="item.valueResourceId" 
                      @change="setProperties" :disabled="!item.valueResourceId">
                      <span class="type-value">{{item.attrValue}}</span>
                      <div class="el-icon-delete del" 
                        @click.stop="deleteCustomStandardValue(item,index,index2)" v-if="item.attrType===2">
                      </div>
                    </el-checkbox>
                  </div>
                </li>
              </ul>
              <ul v-else class="clearfix">
                <li v-for="(item,index2) in group.attrValueSet" :key="item.attrCode" class="type-checkbox-items">
                  <el-checkbox :label="item.attrCode"  @change="setProperties">
                    <span class="type-value">{{item.attrValue}}</span>
                    <div class="el-icon-delete del" @click.stop="deleteCustomStandardValue(item,index,index2)" v-if="item.attrType===2"></div>
                  </el-checkbox>
                </li>
              </ul>
            </div>
          </section>
          <div style="margin:20px 0 0 0" v-if="!loading">
             <cms-button icon='add' @click="popoverVisible=true" size="small"
               v-if="formData.productType.custom">添加规格
              </cms-button>
           </div>
          <!-- 添加规格弹窗 -->
          <el-dialog title="添加规格" top="35vh" :visible.sync='popoverVisible' width="640px" :before-close="handleClose">
            <div class="dialog-form">
              <el-form :model="popoverForm" ref='dialogForm'>
                <el-form-item label="规格名称" prop="name"  label-width="120px" :rules="
                [{ required: true, message: '必须输入规格名称', trigger: 'blur' }]">
                  <el-input v-model="popoverForm.name" maxlength="50"></el-input>
                </el-form-item>
                <el-form-item label="规格值" label-width="120px"
                prop="attrValues"
                :rules=" [{ required: true, message: '必须输入规格值', trigger: 'blur' }]"
                >
                 <el-input v-model="popoverForm.attrValues" type='textarea' 
                  :rows="4" placeholder="多个属性按回车输入" maxlength="80"></el-input>
                </el-form-item>
                <div class="center">
                  <cms-button @click="addCustomStandardItem" style="margin-right:10px;">确认</cms-button>
                  <cms-button @click="clearCustomStandardItem" type='info'>取消</cms-button>
                </div>
              </el-form>
            </div>
          </el-dialog>
        </el-checkbox-group>
      </el-form-item>
      <!-- sku信息 -->
      <el-form-item label="sku信息" prop="name" v-if='formData.coreModel.hasSku&&skus.length>0'>
        <table class="sku-table">
          <tr>
            <th v-for="(item,index) in properties" :key="index" v-if="item.attrValueList.length>0" rowspan='2'>
              {{item.name}}
            </th>
            <th><i class="goods-astrick">*</i>售价</th>
            <th>市场价</th>
            <th>成本价</th>
            <th><i class="goods-astrick">*</i>库存</th>
            <th>预警值</th>
            <th rowspan='2' width='60'>操作</th>
          </tr>
          <tr>
            <td>
              <el-input class="table-input"  :min="0" v-model="batch.salesPrice" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('salesPrice',1)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.marketPrice" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('marketPrice',1)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input"  :min="0" v-model="batch.costPrice" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('costPrice',1)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input"  :min="0" v-model="batch.inventory" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('inventory',2)"></i>
                </template>
              </el-input>
            </td>
            <td>
              <el-input class="table-input" :min="0" v-model="batch.warning" maxlength="8">
                <template slot="append">
                  <i class="iconfont icon-zhankai1-copy" @click="batchPrice('warning',3)"></i>
                </template>
              </el-input>
            </td>
          </tr>
          <tr v-for="(item,index2) in skus" :key="index2">
            <td v-for="(name,index3) in item.name" :key="index3" class="min-w-90">
              {{name}}
            </td>
            <td>
              <el-input v-model="item.salesPrice" maxlength="8" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.marketPrice" maxlength="8" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.costPrice" maxlength="8" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.inventory" maxlength="8" :min="0"></el-input>
            </td>
            <td>
              <el-input v-model="item.warning" maxlength="8" :min="0"></el-input>
            </td>
            <td>
              <i class="el-icon-delete del" @click.stop="deleteSkus(item,index2)"></i>
            </td>
          </tr>
        </table>
        <span class="gray">注：商品实际销售SKU不存在的组合，可删除，系统将默认保存</span>
      </el-form-item>
      <!-- 商品属性信息 -->
      <el-form-item label="商品属性" prop="name">
        <section class="standard-group" v-for="(group,index) in productStandardInfos" :key="group.id">
          <div class="standard-group-header">
            <div class="fl">
              <span class="standard-group-name">{{group.attrGroupName}}</span>
            </div>
            <div class="fr">
              <label class="standard-group-desc">规格值名称</label>
              <el-input style="width:110px" v-model="customAttributeValue['value'+index]" maxlength="30"></el-input>
              <cms-button size='small' style="margin-left:10px;" @click="addCustomAttribute(index)">添加</cms-button>
            </div>
          </div>
          <div class="standard-group-body" style="padding:20px;">
            <!-- 属性组里面的属性项遍历 -->
            <div class="clearfix" :class="[standard.valueType==1?'w-50':'w-100']" v-for='(standard,index2) in group.attrs' :key="standard.id">
              <label class="fl  attribute-name">
                <i class="goods-astrick" v-show="isAttrRequired(standard)">*</i>
                {{standard.name}}
              </label>
              <!-- 多选 -->
              <ul class="fl" v-if="standard.valueType===3 && standard.attrValueSet.length > 0">
                <el-checkbox-group  v-model="attributeGroups[standard.id+'s']">
                <li v-for="(item) in standard.attrValueSet" :key="item.id" class="type-checkbox-items">
                  <el-checkbox :label="item.attrCode" @change="setProductInfos(item,standard,1)">
                    <span class="type-value">{{item.attrValue}}</span>
                  </el-checkbox>
                </li>
                </el-checkbox-group>
              </ul>
              <!-- 单选 -->
              <ul class="fl" v-if="standard.valueType===2 && standard.attrValueSet.length > 0">
                <!-- 加一个字符串让他变成对象0.0 -->
                <el-radio-group v-model="attributeGroups[standard.id+'s']">
                  <li v-for="(item) in standard.attrValueSet" :key="item.id" class="type-radio-items">
                    <el-radio :label="item.attrCode" @change="setProductInfos(item,standard,2)">
                      <span class="type-value">{{item.attrValue}}</span>
                    </el-radio>
                  </li>
                </el-radio-group>
              </ul>
              <!-- 文本 -->
              <el-input v-if="standard.valueType===1" maxlength="50"
               @blur='setProductInfos($event,standard,3)' 
              v-model="attributeGroups[standard.id+'s']"  
              style="width:220px;position: relative;top:0px;"></el-input>
              <div class="el-icon-delete del" @click="deleteCustomAttribute(standard,index,index2)" v-if="standard.attrType==2"></div>
            </div>
          </div>
        </section>
      </el-form-item>
    </el-form>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button v-color @click="prevStep()"  style="background:#f7f7f7;color:#444">上一步</cms-button>
      <cms-button @click="update()" style="background:#efae00">立即发布</cms-button>
    </div>
  </div>
</template>
  <script>
import rules from '@/rules'
import { productStandardDelete } from '@/api/product/type.js'
import {
  productUpdate,
  customStandard,
  customStandardDelete,
  customAttribute,
  customStandardItem,
  productSkuDelete
} from '@/api/product/product.js'
import { CreateSkus } from '@/utils/descartes'
var defaultData = {
  coreModel: {
    salePrice: '',
    marketPrice: 0.0,
    costPrice: 0.0,
    inventory: 0,
    warningValue: 0,
    hasSku: false
  },
  skuTypeAttrs: [],
  skus: [],
  proAttrs: []
}
export default {
  data() {
    var validatePrice = (rule, value, callback) => { // 验证售价
      if (!this.formData.coreModel.hasSku) {
        if (parseFloat(value) <= 0) {
          callback(new Error('请输入大于0的数字'))
        } else {
          callback()
        }
      } else {
        callback()
      }
    }
    var validateNum = (rule, value, callback) => { // 验证库存预警值是否大于库存量
      const reg = /^[1-9]\d*$/
      if (this.formData.coreModel.hasSku) {
        callback()
      } else if (value === '') {
        callback(new Error('请输入库存量'))
      } else if (this.formData.coreModel.warningValue === '' || this.formData.coreModel.warningValue === 0) {
        callback()
      } else if (value < this.formData.coreModel.warningValue) {
        callback(new Error('库存量必须大于库存预警值'))
      } else if (!reg.test(value)) {
        callback(new Error('请输入大于0的整数'))
      } else {
        callback()
      }
    }
    var validateWarn = (rule, value, callback) => { // 验证库存预警值格式
      const reg = /^[0-9]{0,8}$/
      if (value === '') {
        callback()
      } else if (!reg.test(value)) {
        callback(new Error('库存预警值只能为正整数'))
      } else {
        callback()
      }
    }
    return {
      loading: false,
      status: false,
      fixed: true,
      popoverVisible: false,
      popoverForm: {
        name: '',
        attrValues: ''
      },
      storeId: 0, // 这里要取上一步的商城id
      productTypeId: 0, // 这里要取上一步商品类型的id
      loadingTitle: '正在保存,请勿进行其他操作',
      defaultData: {},
      formData: Object.assign({}, defaultData),
      batch: {
        salesPrice: '',
        costPrice: '',
        inventory: '',
        marketPrice: '',
        warning: ''
      },
      rules: {
        'coreModel.salePrice': [rules.required('必须输入一个价格'), rules.float(), rules.decimal(), rules.custom(validatePrice)],
        'coreModel.marketPrice': [rules.float(), rules.decimal()],
        'coreModel.costPrice': [rules.float(), rules.decimal()],
        'coreModel.inventory': [rules.required('请输入库存量'), rules.custom(validateNum)],
        'coreModel.warningValue': [rules.custom(validateWarn)],
        'coreModel.hasSku': [rules.required()]
      },
      customStandardValue: {}, // 自定义规格值
      customAttributeValue: {}, // 自定义属性值
      checkSaleStandardIds: [], // 选中的规格id
      standardImageUrls: {}, // 选中的规格对应图片，对象结构{"规格code":"规格图片地址"}
      properties: [], // 选中的商品规格
      attributes: [], // 选中的属性值
      saleStandardInfos: [], // 销售属性集合(规格列表saleAttrs)
      productStandardInfos: [], // 商品属性集合
      skus: [], // 生成的sku信息
      attributeGroups: {}
    }
  },
  computed: {
    allCheckedLength: function() {
      // 判断勾选改变sku信息
      var len = 0
      this.properties.forEach(v => {
        len = len + v.attrValueList.length
      })
      return len
    }
  },
  methods: {
    initData() { // 初始化数据,在这里将数据处理完成
      const productInfos = this.$store.state.product.productInfo
      this.storeId = this.$store.state.product.storeId
      this.productTypeId = this.$store.state.product.productTypeId
      this.formData.coreModel = productInfos.coreModel// 设置基础价格
      this.formData.skus = productInfos.skus// 设置基础价格
      this.formData.productType = productInfos.productType// 设置是否开启商品规格
      this.saleStandardInfos = productInfos.proTypeAttrItems.saleAttrs// 规格列表
      this.productStandardInfos = productInfos.proTypeAttrItems.nsaleAttrs
      this.attributes = productInfos.proAttrs// 设置属性()
      this.properties = productInfos.skuTypeAttrs
      this.setDefaultProperties()// 默认选中的规格
      this.setDefaultAttributes(productInfos.proAttrs)
      this.setRadioProperties(productInfos.proTypeAttrItems.saleAttrs)// 设置商品规格单选框所绑定的数组
      this.defaultData = productInfos
    },
    deleteSkus(item, index) { // 删除sku组合
      this.skus.splice(index, 1)
      this.sysDeletedSkus()
      if (item.id) { // 若是删除已存在的sku。则需要调接口
        productSkuDelete({ ids: [item.id] }).then(res => {})
      }
    },
    sysDeletedSkus() { // 规格列表同步已删除的sku
      let skuAttrCodes = []// 暂存所有的sku的attrCode码
      this.skus.forEach(val => {
        skuAttrCodes = skuAttrCodes.concat(val.attrCodes)
      })
      this.properties.forEach((v, i) => {
        v.attrValueList.forEach((vv, j) => { // 去属性值列表里去找到对应的属性值
          if (!skuAttrCodes.includes(vv.attrCode)) { // 若skus里面这个属性值不再关联到商品规格，则移除该项
            const checkdIndex = this.checkSaleStandardIds.indexOf(vv.attrCode)
            this.checkSaleStandardIds.splice(checkdIndex, 1)
            v.attrValueList.splice(j, 1)
          }
        })
      })
    },
    // sysDeletedSkus(item) { // 规格列表同步已删除的sku
    //   this.properties.forEach((v, i) => {
    //     v.attrValueList.forEach((vv, j) => { // 去属性值列表里去找到对应的属性值删除
    //       if (item.attrCodes.includes(vv.attrCode)) { // 匹配到所在的属性列表
    //         if (v.valueType !== 2) { // 非单选框时
    //           const checkdIndex = this.checkSaleStandardIds.indexOf(vv.attrCode)
    //           this.checkSaleStandardIds.splice(checkdIndex, 1)
    //           v.attrValueList.splice(j, 1)
    //         }
    //         //  else if (v.valueType === 2) { // 单选类型时
    //         //   if (this.skus.length === 1) {
    //         //     this.saleStandardInfos[i].radioValue = ''
    //         //     this.properties[i].attrValueList = []
    //         //   }
    //         // }
    //       }
    //     })
    //   })
    // },
    setDefaultProperties() { // 设置默认选中的规格项
      this.properties.forEach(v => {
        v.attrValueList.forEach(item => {
          this.checkSaleStandardIds.push(item.attrCode)// 选中的id
          this.standardImageUrls[item.attrCode] = item.url// 选中的图片
        })
      })
    },
    setRadioProperties(data) { // 设置商品规格单选框所绑定的字符串及回填
      data.forEach(v => {
        if (v.valueType === 2) {
          v.radioValue = ''
          v.attrValueSet.forEach(item => {
            if (item.isChecked) { // 回填选中的单选框
              v.radioValue = item.attrCode
            }
          })
        } else if (v.valueType === 4) { // 图片绑定的字段
          v.attrValueSet.forEach(vv => {
            vv.valueResourceId = vv.resourceData ? vv.resourceData.id : ''
          })
        }
      })
    },
    setDefaultAttributes(data) { // 设置选中的属性值
      const obj = {}
      data.forEach(v => {
        if (v.attrValueType === 3) { // 多选类型是还需attrVc的判断长度
          if (v.attrVc.indexOf(',') < 0) {
            obj[v.proTypeAttrId + 's'] = [v.attrVc]
          } else {
            obj[v.proTypeAttrId + 's'] = v.attrVc.split(',')
          }
        } else {
          obj[v.proTypeAttrId + 's'] = v.attrVc
        }
      })
      this.attributeGroups = obj
      return obj
    },
    setRadioSkus(code) {
      this.saleStandardInfos.forEach((v, i) => {
        const tmp = []
        v.attrValueSet.forEach((vv, j) => {
          if (code.indexOf(vv.attrCode) > -1) {
            tmp.push(vv)
          }
        })
        this.properties[i].attrValueList = Object.assign(this.properties[i].attrValueList, tmp)
        this.createdSkus()
      })
    },
    setProperties(item) {
      // 获得选中的规格
      const ids = this.checkSaleStandardIds
      this.saleStandardInfos.forEach((v, i) => {
        const tmp = []
        v.attrValueSet.forEach((vv, j) => {
          if (ids.indexOf(vv.attrCode) > -1) {
            tmp.push(vv)
          }
        })
        this.properties[i].attrValueList = tmp
      })
    },
    validateRequire() { // 验证商品规格必填项
      let flag = true
      if (!this.formData.coreModel.hasSku || this.saleStandardInfos.length === 0) {
        flag = true
      } else { // 若启用了商品规格则验证必填项
        this.saleStandardInfos.forEach((v, i) => { // 根据商品规格名和skuTypeAttrs(properties)的attrValueList长度进行比对
          if (v.required) {
            var item = this.properties[i]
            if (item.attrValueList.length <= 0) {
              this.$errorMessage('请选择' + item.name)
              flag = false
              return
            }
          }
        })
      }
      return flag
    },
    validateAttrRequired() { // 验证商品属性的必填项
      let flag = true
      const ids = []// 获取需要必填的属性id值
      if (this.productStandardInfos.length > 0) {
        this.productStandardInfos.forEach(v => {
          v.attrs.forEach(vv => {
            if (vv.required) {
              ids.push(vv.id)
              if (this.attributes.length < ids.length) {
                flag = false
                this.$errorMessage('请填写必填的商品属性')
              }
              if (this.attributes.length > 0) { // 还需判断值是否为空
                for (let i = 0; i < this.attributes.length; i++) {
                  if (!this.attributes[i].attrVc) {
                    flag = false
                    this.$errorMessage('请填写必填的商品属性')
                    break
                  }
                }
              }
            }
          })
        })
      }
      return flag
    },
    isAttrRequired(obj) { // 判断属性是否必填
      if (obj.required) {
        return true
      } else {
        return false
      }
    },
    setProductInfos(item, parentItem, type) { // 获得选中的属性 type=1多选 2单选 3 文本
      let flag = true // 判断是否是同一个属性的多个值(保证proAttr下不会出现多个相同的属性，但属性值不固定个数)
      const obj = {
        groupName: parentItem.attrGroupName,
        attrName: parentItem.name,
        attrVc: item.attrCode,
        proTypeAttrId: parentItem.id
      }
      if (type === 3) {
        obj.attrVc = item.target.value
      } else {
        obj.attrVc = item.attrCode
      }
      if (this.attributes.length === 0) { // (相当于选中的属性)
        this.attributes.push(obj)
      } else {
        for (const v of this.attributes) {
          if (v.proTypeAttrId === obj.proTypeAttrId) {
            const codeArr = v.attrVc.split(',')// 获取已选中的属性值的code组成的数组
            if (type === 1) {
              // 如果是复选,处理掉相同项
              const indexOf = codeArr.indexOf(obj.attrVc)
              if (indexOf > -1) {
                // 存在就删除
                codeArr.splice(indexOf, 1)
              } else {
                // 不存在就添加
                codeArr.push(obj.attrVc)
              }
              v.attrVc = codeArr.join(',')
            } else {
              // 单选和文本都做覆盖
              v.attrVc = obj.attrVc
            }
            flag = false
            break
          }
        }
        if (flag) {
          this.attributes.push(obj)
        }
      }
    },
    setCoreModel(status) { // 启用商品规格时，根据sku回填售价(取最小值)，库存(取总)等信息
      if (status) { // 启用商品规格时
        let [salePrice, marketPrice, costPrice, inventory, warning] = [[], [], [], [], [], []]// 暂存每个信息组成的数组及初始值
        if (this.formData.skus.length > 0) {
          this.formData.skus.forEach((v, i, arr) => { // 把每一项需要的信息都放到对应的数组里及累加
            salePrice.push(v.salesPrice)
            marketPrice.push(v.marketPrice)
            costPrice.push(v.costPrice)
            inventory.push(v.inventory)
            warning.push(v.warning)
          })
          // 给每一项信息所组成的数组排序及累加
          salePrice = salePrice.sort((a, b) => { return a - b })
          marketPrice = marketPrice.sort((a, b) => { return a - b })
          costPrice = costPrice.sort((a, b) => { return a - b })
          inventory = inventory.reduce((a, b) => { return parseInt(a) + parseInt(b) })
          warning = warning.reduce((a, b) => { return parseInt(a) + parseInt(b) })
          // 填充表格信息
          this.formData.coreModel.salePrice = salePrice[0]
          this.formData.coreModel.marketPrice = marketPrice[0]
          this.formData.coreModel.costPrice = costPrice[0]
          this.formData.coreModel.inventory = inventory
          this.formData.coreModel.warningValue = warning
        }
      }
    },
    createdSkus() { // 构造sku属性信息
      const data = []
      // 把选中的属性过滤好
      this.properties.forEach(v => { data.push(v.attrValueList) })
      const newSkus = CreateSkus(data)
      const oldSkus = this.defaultData.skus
      for (const v in newSkus) {
        const saleAttrCode = newSkus[v].attrCodes.sort((a, b) => { return a - b }).join('-')// 从小到大排序
        for (const vv of oldSkus) { // 这里可以做优化
          if (saleAttrCode === vv.saleAttrCode) {
            vv.attrCodes = vv.saleAttrCode.split('-')
            vv.name = newSkus[v].name
            newSkus[v] = vv
            break
          }
        }
      }
      this.skus = newSkus
    },
    batchPrice(name, type) { // 快速填写商品sku,type为验证的类型
      const reg1 = /^\d{0,8}\.{0,1}(\d{1,2})?$/// 验证价格
      const reg2 = /^[0-9]{1}[0-9]*$/// 验证库存
      const reg3 = /^[0-9]+$/// 验证库存预警值
      this.skus.forEach(v => {
        if (type === 1 && reg1.test(this.batch[name]) && this.batch[name] !== '') {
          v[name] = this.batch[name]
        } else if (type === 2 && reg2.test(this.batch[name])) {
          v[name] = this.batch[name]
        } else if (type === 3 && reg3.test(this.batch[name])) {
          v[name] = this.batch[name]
        } else {
          this.$errorMessage('格式不正确')
        }
      })
    },
    addCustomStandardItem(index) {
      // 商家新增规格
      this.$refs['dialogForm'].validate(valid => {
        if (valid) {
          customStandardItem({
            attrGroupName: '自定义添加',
            name: this.popoverForm.name,
            attrValues: this.popoverForm.attrValues,
            typeId: this.productTypeId,
            storeId: this.storeId,
            valueType: 2
          }).then(res => {
            const tempObj = res.data
            tempObj.attrValueSet = tempObj.attrValueList// 把attrValueList换成attrValueSet
            delete tempObj.attrValueList
            this.saleStandardInfos.push(tempObj)
            // 新增一个规格要同步一下规格属性
            this.properties.push({
              valueType: res.data.valueType,
              attrGroupName: res.data.attrGroupName,
              name: res.data.name,
              id: res.data.id,
              attrValueList: []
            })
            this.popoverVisible = false
            this.popoverForm.name = ''
            this.popoverForm.attrValues = ''
          })
        } else {
          return false
        }
      })
    },
    clearCustomStandardItem() {
      this.popoverVisible = false
      this.popoverForm.name = ''
      this.popoverForm.attrValues = ''
      this.$refs.dialogForm.resetFields()
    },
    addCustomStandard(index) {
      // 商家新增自定义规格值
      if (this.customStandardValue['value' + index] === undefined) { // 先判断规格值名称是否填写
        this.$errorMessage('请输入规格值名称')
      } else {
        const params = {
          attrValue: this.customStandardValue['value' + index],
          storeId: this.storeId,
          attrId: this.saleStandardInfos[index].id
        }
        customStandard(params).then(res => {
          if (res.code === 200) {
            const obj = {
              attrCode: res.data.attrCode,
              attrValue: res.data.attrValue,
              id: res.data.id,
              attrType: res.data.attrType,
              valueResourceId: ''
            }
            this.saleStandardInfos[index].attrValueSet.push(obj)
            this.customStandardValue['value' + index] = ''
          }
        })
      }
    },
    addCustomAttribute(index) {
      // 商家自定义属性值
      if (this.customAttributeValue['value' + index] !== undefined) {
        const params = {
          name: this.customAttributeValue['value' + index],
          attrGroupName: this.productStandardInfos[index].attrGroupName,
          attrValues: 'no',
          typeId: this.productTypeId,
          storeId: this.storeId
        }
        customAttribute(params).then(res => {
          if (res.code === 200) {
            const obj = {
              attrGroupName: res.data.attrGroupName,
              attrValueSet: res.data.attrValueList,
              custom: res.data.custom,
              id: res.data.id,
              name: res.data.name,
              valueType: res.data.valueType,
              attrType: res.data.attrType
            }
            this.productStandardInfos[index].attrs.push(obj)
            this.customAttributeValue['value' + index] = ''
          }
        })
      } else {
        this.$errorMessage('请输入规格值名称')
      }
    },
    deleteCustomStandard(item, index) { // 删除自定义规格
      productStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          this.saleStandardInfos.splice(index, 1)
          this.properties.splice(index, 1)
        }
      })
    },
    deleteCustomStandardValue(item, parentIndex, index) { // 删除自定义规格值
      console.log('parentIndex' + parentIndex)
      console.log('index' + index)
      customStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          const $index = this.checkSaleStandardIds.findIndex(v => v.id === item.id)
          this.checkSaleStandardIds.splice($index, 1)
          this.setProperties(this.checkSaleStandardIds)
          this.saleStandardInfos[parentIndex].attrValueSet.splice(index, 1)
        }
      })
    },
    deleteCustomAttribute(item, parentIndex, index) { // 删除自定义属性
      productStandardDelete({ ids: [item.id] }).then(res => {
        if (res.code === 200) {
          this.productStandardInfos[parentIndex].attrs.splice(index, 1)
        }
      })
    },
    getStandardImg(items, index, params) {
      // 设置图片项
      // 动态设置图片的id
      this.$set(this.standardImageUrls, params.attrCode, items[0].url)
      this.$set(this.saleStandardInfos[params.parentIndex].attrValueSet[index], 'valueResourceId', items[0].id) // 动态设置图片的路径
      this.$set(this.saleStandardInfos[params.parentIndex].attrValueSet[index], 'url', items[0].url)
    },
    prevStep() {
      this.$store.dispatch('setStep', 'step2')
    },
    update() { // 发布商品
      this.$refs.form.validate((valid, item) => {
        if (valid && this.validateRequire() && this.validateSkus() && this.validateAttrRequired()) {
          this.setCoreModel(this.formData.coreModel.hasSku)// 提交的时候覆盖一下coreModel
          const productInfos = this.$cms.deepCopy(this.formData)
          productInfos.skuTypeAttrs = this.properties
          productInfos.skus = this.skus
          productInfos.proAttrs = this.attributes
          this.$store.dispatch('setStandardInfo', productInfos).then(param => {
            this.loading = true
            productUpdate(param).then(res => {
              this.loading = false
              if (res.code === 200) {
                this.$successMessage('修改成功!')
                setTimeout(() => {
                  this.$routerLink('/product/sale', 'list')
                }, 1000)
              }
            })
          })
        } else {
          // 定位到错误的那一行
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key.replace('.', '_')
              break
            }
          }
          if (name) { // 若定位到有错误信息
            this.errorPosition(name)
          }
        }
      })
    },
    errorPosition(name) {
      // 错误信息定位
      const lay = document.querySelector('#layout')
      const ele = this.$refs[name].$el
      lay.scrollTop = ele.offsetTop - 350
      return false
    },
    validateSkus() { // 验证验证用户所填写的sku信息是否符合规范
      let flag = true
      if (this.formData.coreModel.hasSku) { // 若启用的商品规格
        const reg1 = /^\d{0,8}\.{0,1}(\d{1,2})?$/// 验证价格
        const reg2 = /^[0-9]{1}[0-9]*$/// 验证库存
        const reg3 = /^[0-9]+$/// 验证库存预警值
        if (this.skus.length > 0) {
          this.skus.forEach(item => { // 先循环数组,获取每一条对象
            if (!reg1.test(item.salesPrice) || !reg1.test(item.costPrice) || !reg1.test(item.marketPrice)) {
              flag = false
              this.$errorMessage('sku价格信息不符合规范')
            } else if (!reg2.test(item.inventory) || !reg3.test(item.warning)) {
              flag = false
              this.$errorMessage('sku库存信息不符合规范')
            } else if (parseFloat(item.salesPrice) <= 0) {
              flag = false
              this.$errorMessage('sku信息中售价不能小于0')
            } else if (parseInt(item.warning) !== 0) { // 若修改了库存预警则需要判断库存的的关系
              if (parseInt(item.inventory) < parseInt(item.warning)) {
                flag = false
                this.$errorMessage('sku信息中库存预警值不能大于库存量')
              }
            }
          })
        }
      }
      return flag
    },
    goTop() { // 返回顶部
      const lay = document.querySelector('#layout')
      lay.scrollTop = 0
    },
    handleClose() {
      this.popoverVisible = false
      this.$refs.dialogForm.resetFields()
      this.popoverForm.name = ''
      this.popoverForm.attrValues = ''
    }
  },
  watch: {
    allCheckedLength: {
      // 监听选中的变化 重新触发sku信息
      handler() {
        this.createdSkus()
      }
    }
  },
  activated() {
    this.goTop()
    this.initData()
    this.setCoreModel(this.formData.coreModel.hasSku)// 根据是否设置sku来回填信息库存预警等(默认触发一次)
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>
.w-50 {
  width: 50%;
  display: inline-block;
  margin: 5px 0;
}
.w-100 {
  width: 100%;
  display: block;
}
.attribute-name {
  width: 120px;
  margin-right: 25px;
  font-size: 14px;
  color: #666;
  text-align: right;
}
.icon-zhankai1-copy {
  cursor: pointer;
}
.del{
  cursor: pointer;
  font-size: 14px;
  color: #a3a3a3;
  &:hover{
    color: #fe5f40;
  }
}
.goods-astrick{
  color: #f56c6c;
  position: relative;
  right: 4px;
}
</style>




