  <!--商品信息项-->
  <template>
  <div
    class="cms-form-layout loading-top product-main-warpper"
    v-loading.fullscreen="loading"
    element-loading-text="正在加载..."
  >
    <el-form ref="form" :rules="rules" :model="formData" label-width="210px" @submit.native.prevent
     style="margin-bottom:48px;">
      <h3 class="cms-segmentation">基本信息</h3>
      <el-form-item label="商品分类" prop="product.categoryId">{{categoryName}}</el-form-item>
      <el-form-item label="店铺分类" prop="product.storeCategoryId">
        <el-cascader
          :options="options"
          :props="props"
          :change-on-select="true"
          v-model="formData.storeCategoryIds"
          placeholder="请选择分类"
        ></el-cascader>
      </el-form-item>
      <el-form-item label="商品名称" prop="product.name" ref="product.name">
        <el-input v-model="formData.product.name" maxlength="100" 
        type="textarea" resize="none" spellcheck="false"
        ></el-input>
        <span class="gray">商品名称长度控制在100个字符以内</span>
      </el-form-item>
      <!-- 通过类型判断是否为虚拟商品 -->
      <el-form-item label="虚拟规格商品" prop="product.virtualCardTplId" v-if="isVirtual">
        <el-select v-model="formData.product.virtualCardTplId" :popper-append-to-body="false">
          <el-option v-for="item in roleList" :label="item" :value="item" :key="item"></el-option>
        </el-select>
        <span class="gray">选择虚拟库（卡密）模板即可，点击新建模板增加</span>
      </el-form-item>
      <el-form-item label="货号" prop="product.itemNumber">
        <el-input v-model="formData.product.itemNumber" maxlength="30"></el-input>
        <span class="gray">货号用于商家管理商品的一种编号，仅供商家使用，最多输入30个字符</span>
      </el-form-item>
      <el-form-item label="品牌" prop="product.brandId">
        <cms-brand
          :select="[formData.product.brand]"
          v-model="formData.product.brandId"
          v-if="status"
        >
          <cms-button type="info" size="small">选择品牌</cms-button>
        </cms-brand>
      </el-form-item>
      <el-form-item label="商品简介" prop="product.summary" class="product-profile">
        <el-input type="textarea" :rows="4" v-model="formData.product.summary" maxlength="100"></el-input>
        <el-color-picker v-model="formData.product.summaryColor" class="product-color-picker"></el-color-picker>
        <span class="gray">商品简介显示在商品标题下方,长度不超过100字符，右侧的颜色板可设置显示颜色</span>
      </el-form-item>
      <el-form-item label="运费模板" prop="product.templeteId" ref="product.templeteId" class="add-select-padd">
        <el-select v-model="formData.product.templeteId" :popper-append-to-body="false" @change="changeTemplate">
          <el-option
            :label="item.expressName"
            :value="item.id"
             v-for="item in freightItems"
            :key="item.id"
          ></el-option>
        </el-select>
        <table class="sku-table" style='width:380px;margin-top:10px;'>
            <tr>
              <th>运送方式</th>
              <th>运送到</th>
              <th>首件(个)</th>
              <th>运费</th>
              <th>续建(个)</th>
              <th>续费</th>
            </tr>
            <tbody v-for="(items,index) in freightInfo.tplDetails" :key="index" 
              :style="{'background-color':freightInfo.isFree?'#5CB85C':'#CCCCCC'}">
              <tr v-for="(freight,indexs) in items.tplDetails" :key="indexs">
                <td :rowspan="items.tplDetails.length" v-if="indexs==0">{{items.deliveryWay|deliveryWayFilter}}</td>
                <td>{{freight.districtidList|districtFilter}}</td>
                <td>{{freight.firstNum}}</td>
                <td>{{$cms.float(freight.firstPrice)}}</td>
                <td>{{freight.addNum}}</td>
                <td>{{$cms.float(freight.addPrice)}}</td>
              </tr>
            </tbody>
            <tr style="background-color:#5CB85C" v-if="freightInfo.isFree">
              <td colspan="6">卖家承担邮费</td>
            </tr>
        </table>
      </el-form-item>
      <el-form-item label="体积" prop="product.volume" ref="product.volume">
        <el-input v-model="formData.product.volume" maxlength="8">
          <template slot="append">立方米</template>
        </el-input>
      </el-form-item>
      <el-form-item label="重量" prop="product.weight" ref="product.weight">
        <el-input v-model="formData.product.weight" maxlength="8">
          <template slot="append">kg</template>
        </el-input>
      </el-form-item>
      <h3 class="cms-segmentation">商品相册</h3>
      <el-form-item label="短视频"  ref="videoResources">
        <cms-picture-zone
          type="video"
          viewType="media"
          :data="formData.videoResources[0]"
          :props="{id:'vdResourceId',url:'url'}"
          @change="getProductVideo"
        >
        <cms-button type="info" size="small">选择视频</cms-button>
        </cms-picture-zone>
      </el-form-item>
      <el-form-item
        label="商品相册"
        prop="imageResources"
        ref="imageResources"
        :error="errorMessage.imageResources"
        class="imageResource"
      >
        <div class="clearfix">
          <!-- 拖拽组件 -->
          <draggable v-model="formData.imageResources" :options="{animation:150}">
            <cms-picture-zone
              class="product-img-box"
              v-for="(item,index) in formData.imageResources"
              :key="index"
              :preview="false"
              :index="index"
              @change="getProductImg"
            >
              <div class="product-img-warpper">
                <img
                  v-if="formData.imageResources[index]&&formData.imageResources[index].url!==''"
                  :src="$cms.filterUrl(formData.imageResources[index].url)"
                  alt
                >
                <span v-else class="img-plus el-icon-plus"></span>
              </div>
              <!-- 图片位置移动 -->
              <div class="img-move-bar" @click.stop>
                <span
                  class="el-icon-arrow-left next"
                  :class="[index===0?'disable':'']"
                  @click.stop="imgMove(index,'next')"
                ></span>
                <span
                  class="el-icon-arrow-right next"
                  :class="[index===4?'disable':'']"
                  @click.stop="imgMove(index,'prev')"
                ></span>
                <span class="el-icon-close delete" @click.stop="imgDelete(index)"></span>
              </div>
            </cms-picture-zone>
          </draggable>
        </div>
        <span
          class="gray"
          style="max-width:490px;"
        >用于显示在商品详情页介绍图，最多可支持7张，第一张默认为商品主图用于显示商品列表，拖动照片可调整图片顺序</span>
      </el-form-item>
      <h3 class="cms-segmentation">详细信息</h3>
      <el-form-item label="" prop="checked" v-show="txtModel=='pc'" label-width="0">
        <cms-editor v-model="formData.txt.pcTxt" v-if="status"></cms-editor>
      </el-form-item>
      <el-form-item label="顶部版式" prop="txt.topTplId" v-show="txtModel=='pc'" class="add-select-padd">
          <el-select v-model="formData.txt.topTplId" :popper-append-to-body="true" >
          <el-option v-for="item in formatItems" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
        <span class="gray">选中的版式将插入至PC端商品详情最顶部</span>
      </el-form-item>
      <el-form-item label="底部版式" prop="txt.bottomTplId" v-show="txtModel=='pc'" class="add-select-padd">
        <el-select v-model="formData.txt.bottomTplId" :popper-append-to-body="false">
          <el-option v-for="item in formatItems" :label="item.name" :value="item.id" :key="item.id"></el-option>
        </el-select>
        <span class="gray">选中的版式将插入至PC端商品详情最底部</span>
      </el-form-item>
      <h3 class="cms-segmentation">其他信息</h3>
      <el-form-item label="商品单位" prop="ext.unit">
        <el-input v-model="formData.ext.unit" maxlength="5"></el-input>
        <span class="gray">可输入件、盒、份、个，默认为件</span>
      </el-form-item>
      <el-form-item label="商品服务" prop="ext.storeServiceIds">
        <el-checkbox-group v-model="formData.ext.storeServiceId">
          <el-checkbox v-for="item in serviceItems" :key="item.id" :label="item.id">{{item.serviceName}}</el-checkbox>
        </el-checkbox-group>
      </el-form-item>
      <el-form-item label="是否为销售商品出售" prop="product.sellGoodsSale" ref="product.sellGoodsSale">
        <cms-switch v-model="formData.product.sellGoodsSale"></cms-switch>
      </el-form-item>
      <el-form-item label="是否为提供发票" prop="product.isInvoice" ref="product.isInvoice">
        <cms-switch v-model="formData.product.isInvoice"></cms-switch>
      </el-form-item>
      <el-form-item label="商品关键词" prop="ext.keyword">
        <el-input v-model="formData.ext.keyword" placeholder="请输入商品关键词" 
          type="textarea"  resize="none" maxlength="200">
        </el-input>
        <span class="gray">设置检索关键词,用于商品搜索,最多支持5个，使用','分隔</span>
      </el-form-item>
      <el-form-item label="评价标签" prop="ext.evaluationView">
        <el-input v-model="formData.ext.evaluationView" placeholder="请输入评价标签" 
         type="textarea"  resize="none" maxlength="200">
        </el-input>
        <span class="gray">买家评价商品时，可对商品选择对应标签进行评价，如性价比高，使用','分隔</span>
      </el-form-item>
    </el-form>
    <div class="product-footer" :class="[fixed?'fixed-footer':'']">
      <cms-button v-color @click="nextStep(true)">下一步,设置规格/属性信息</cms-button>
      <cms-button @click="nextStep(false)" style="background:#efae00">立即保存</cms-button>
    </div>
  </div>
</template>
  <script>
import axios from 'axios'
import rules from '@/rules'
import draggable from 'vuedraggable'
import { productCategoryGet } from '@/api/product/category.js'
import {
  storeCategoryTree,
  storeServiceList
} from '@/api/shop/shopManage.js' // 商家商品分类
import { freightList, freightGet } from '@/api/shop/freight.js'
import {
  productGet,
  ckItemNumber,
  productUpdate,
  productFormatList
} from '@/api/product/product.js'
var defaultData = {
  storeServiceIds: [],
  product: {
    storeId: '',
    storeCategoryId: '',
    categoryId: '',
    name: '',
    virtualCardTplId: '',
    itemNumber: '',
    brandId: 1,
    summary: '',
    summaryColor: '#ff0000',
    templeteId: 1,
    weight: '',
    volume: '',
    sellGoodsSale: true,
    isInvoice: true,
    status: 3
  },
  productTypeId: '',
  storeCategoryIds: [],
  imageResources: [
    // 最多五张图片
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    },
    {
      remark: '',
      imgResourceId: '',
      url: ''
    }
  ],
  videoResources: [
    {
      remark: '',
      vdResourceId: '',
      url: ''
    }
  ],
  txt: {
    pcTxt: 'pc端内容',
    mobileTxt: '移动端内容'
  },
  ext: {
    unit: '件',
    keyword: '',
    evaluationView: ''
  }
}
var categoryNames = []
export default {
  components: { draggable },
  data() {
    var checkCode = (rule, value, callback) => {
      if (value === '') {
        callback()
      } else {
        ckItemNumber({
          itemNumber: value,
          storeId: this.formData.product.storeId,
          id: this.formData.product.id
        }).then(res => {
          if (res.data) {
            callback()
          } else {
            callback(new Error('货号重复'))
          }
        })
      }
    }
    return {
      loading: false,
      status: false,
      fixed: true,
      txtModel: 'pc',
      storeItems: [], // 自营店铺列表
      options: [],
      props: {
        value: 'id',
        label: 'cateName',
        children: 'childs',
        disabled: 'isAuth'
      },
      freightItems: [], // 运费模板列表
      freightInfo: {}, // 运费模板详情
      serviceItems: [], // 售后服务保障列表
      formatItems: [], // 商品版式（顶部和底部）
      formData: Object.assign({}, defaultData),
      categoryName: '',
      isVirtual: false, // 判断是否是虚拟分类
      rules: {
        'product.templeteId': [
          rules.required('必须选择一个运费模板', 'change')
        ],
        'product.categoryId': [rules.required('必须选择一个商品分类')],
        'product.name': [
          rules.required('请输入商品名称'),
          rules.max(100, '最多只能输入100个字符')
        ],
        'product.summary': [rules.max(100, '最多只能输入100个字符')],
        'product.itemNumber': [rules.custom(checkCode)],
        'product.volume': [rules.required(), rules.float(), rules.decimal()],
        'product.weight': [rules.required(), rules.float(), rules.decimal()],
        'imageResources': [rules.required('至少上传一张商品图片')],
        'product.sellGoodsSale': [rules.required()],
        'product.isInvoice': [rules.required()]
      },
      errorMessage: {}
    }
  },
  methods: {
    initData() {
      // 初始化商品信息
      this.loading = true
      productGet({ id: this.$route.query.productId }).then(res => {
        if (res.code === 200) {
          // 初始化商品数据
          this.formData.product = res.data.product
          this.formData.txt = res.data.txt
          this.formData.ext = res.data.ext
          this.setStoreData(res.data)
          this.getRelevantInfos(this.formData.product.category.id) // 加载相关的接口
          this.getStroeCategory(res.data.product.storeId) // 手动触发一下分类
          this.changeTemplate(this.formData.product.templeteId)// 激活时获取运费模板信息
        } else {
          this.loading = false
        }
      })
    },
    getStroeCategory(id) {
      // 加载对应的商家商品分类
      storeCategoryTree().then(res => {
        this.options = []
        const root = []
        this.options = root.concat(res.data)
      })
    },
    setStoreData(data) {
      // 拆解返回的信息,参考文档处理
      const params = this.$cms.deepCopy(data)
      const imgArr = []
      const videoArr = [
        {
          remark: '',
          vdResourceId: '',
          url: ''
        }
      ]
      this.formData.storeCategoryIds = params.product.storeProductCategory
        ? params.product.storeProductCategory.nodeIds
        : ['']
      params.resources.forEach(v => { // 拆解视频图片资源集合
        if (v.videoResource && v.videoResource.id !== '') {
          const obj = {
            remark: '',
            alias: v.videoResource.alias,
            vdResourceId: v.videoResource.id,
            url: v.videoResource.url
          }
          videoArr[0] = obj
        } else if (v.imageResouce && v.imageResouce.id !== '') {
          const obj2 = {
            remark: '',
            imgResourceId: v.imageResouce.id,
            url: v.imageResouce.url
          }
          imgArr.push(obj2)
        }
      })
      this.formData.product.categoryId = this.formData.product.category.id // 设置分类id
      params.product.categoryId = params.product.category.id // 设置分类id
      this.getCategoryName(params.product.category) // 设置分类名称
      this.formData.imageResources = Object.assign(
        this.formData.imageResources,
        imgArr
      )
      params.imageResources = Object.assign(
        this.formData.imageResources,
        imgArr
      )
      this.formData.videoResources = videoArr
      params.videoResources = videoArr
      this.formData.ext.keyword = params.ext.keyword
      this.formData.ext.evaluationView = params.ext.evaluationView
      // if (params.product.brand) {
      //   // 设置品牌id
      //   this.formData.product.brandId = params.product.brand.id
      //   params.product.brandId = params.product.brand.id
      // } else {
      //   this.formData.product.brandId = ''
      //   params.product.brandId = ''
      // }
      // // 设置商品服务信息(提取保障服务的id)
      this.$set(this.formData.ext, 'storeServiceId', this.transferServiceIds(params.proConsumerServices))
      // 设置规格属性
      params.skuTypeAttrs = this.createSkuTypeAttrs(
        params.proTypeAttrs.saleAttrs,
        params.skuTypeAttrs
      )
      params.skus = this.createSku(params.skus)
      params.proTypeAttrItems = params.proTypeAttrs // 转换下属性
      params.proTypeAttrs = params.proAttrs
      delete params.product.category
      delete params.product.brand
      this.$store.dispatch('setProductInfo', params)
      this.status = true // 需要数据加载完成后加载编辑器那些
      return params
    },
    transferServiceIds(data) { // 转换商品保障服务id
      const arr = []
      if (data instanceof Array && data.length > 0) { // 判断数据是否是数组
        data.forEach(v => {
          arr.push(v.id)
        })
      }
      return arr
    },
    createSku(data) {
      const skus = []
      data.forEach((v, i) => {
        skus.push(v)
        skus[i].attrCodes = v.saleAttrCode.split('-')
      })
      return skus
    },
    createSkuTypeAttrs(saleStandardInfos, data) {
      // 设置规格属性信息
      const properties = []
      saleStandardInfos.map(v => {
        properties.push({
          valueType: v.valueType,
          attrGroupName: v.attrGroupName,
          name: v.name,
          id: v.id,
          attrValueList: []
        })
      })

      data.forEach((v, i) => {
        properties.forEach((item, j) => {
          if (v.productTypeAttr.id === item.id) {
            const obj = {
              url: v.resourceData ? v.resourceData.url : '',
              valueResourceId: v.resourceData ? v.resourceData.id : '',
              attrCode: v.attrValueCode,
              attrValue: v.attrValueName
            }
            item.attrValueList.push(obj)
          }
        })
      })
      return properties
    },
    getCategoryName(obj) {
      // 设置分类名称
      categoryNames.push(obj.cateName)
      if (obj.parent) {
        this.getCategoryName(obj.parent)
      } else {
        const arr = categoryNames.reverse()
        arr.forEach((v, i) => {
          if (i === 0) {
            this.categoryName = v
          } else {
            this.categoryName = this.categoryName + ' > ' + v
          }
        })
      }
    },
    getRelevantInfos(categoryId) {
      // 这里获取配套的列表信息
      axios
        .all([
          productCategoryGet({ id: categoryId }),
          productFormatList(),
          freightList(),
          storeServiceList()
        ])
        .then(
          axios.spread(
            (productCategory, formatItems, freightItems, serviceItems) => {
              this.formatItems = formatItems.data // 商品版式（顶部和底部）
              this.formData.productTypeId = productCategory.data.productTypeId // 设置商品类型id
              this.isVirtual = productCategory.data.virtual // 设置是否是虚拟分类
              this.freightItems = freightItems.data// 获取运费模板信息
              this.serviceItems = serviceItems.data// 获取商品服务列表
              this.loading = false
            }
          )
        )
    },
    validateImgs() {
      // 验证是否上传了图片
      let state = false
      this.formData.imageResources
      for (const v of this.formData.imageResources) {
        if (v.imgResourceId !== '') {
          state = true
        }
      }
      if (state) {
        return true
      } else {
        this.errorMessage = {}
        setTimeout(() => {
          this.$set(
            this.errorMessage,
            'imageResources',
            '至少上传一张商品图片'
          )
          this.errorPosition('imageResources')
        }, 1)
        return false
      }
    },
    nextStep(status) {
      // 是否立即保存
      // 下一步操作
      this.$refs.form.validate((valid, item) => {
        if (valid) {
          if (this.validateImgs()) {
            // 判断图片是否上传
            const obj = this.formatFormData(this.formData)
            this.$store.dispatch('setBaseProductInfo', obj).then(res => {
              // 把商品服务的数组再转成后台要的字符串
              if (this.formData.ext.storeServiceId.length > 0) {
                this.formData.ext.storeServiceId = this.formData.ext.storeServiceId.join(',')
              }
              this.$store.dispatch('setStandardInfo', {}).then(param => {
                this.loading = true
                productUpdate(param).then(res => {
                  this.loading = false
                  if (res.code === 200) {
                    if (status) {
                      this.$store.dispatch('setStep', 'step3')
                    } else {
                      this.$successMessage('修改成功!')
                    }
                  } else {
                    this.$errorMessage(res.message)
                  }
                })
              })
            })
          }
        } else {
          // 定位到错误的那一行
          let name = ''
          for (const key in item) {
            if (item.hasOwnProperty(key)) {
              name = key
              break
            }
          }
          this.errorPosition(name)
        }
      })
    },
    formatFormData(data) {
      // 把一些后台需要字符串的数据给转化一下
      const obj = Object.assign({}, data)
      obj.ext.evaluation = obj.ext.evaluationView
      if (obj.storeServiceIds.length > 0) {
        obj.proConsumerServices = obj.storeServiceIds.join(',')
      }
      // 设置店铺分类
      obj.product.storeCategoryId =
        data.storeCategoryIds[data.storeCategoryIds.length - 1]
      delete obj.product.storeProductCategory// 删除后台不需要的字段(注意)
      return obj
    },
    errorPosition(name) {
      // 错误信息定位
      const lay = document.querySelector('#layout')
      const ele = this.$refs[name].$el
      lay.scrollTop = ele.offsetTop - 350
      return false
    },
    getProductVideo(item, index) {
      if (item) { // 上传
        const extention = item[0].url.substring(item[0].url.lastIndexOf('.') + 1)// 获取后缀名
        const extentionArr = ['mp4', 'ogg']
        console.log(extentionArr.includes(extention))
        if (extentionArr.includes(extention)) { // 验证上传文件的格式
          if (this.formData.videoResources.length > 0) {
            this.$set(this.formData.videoResources, index, {
              remark: '',
              vdResourceId: item[0].id,
              url: item[0].url
            })
          }
        } else {
          this.$errorMessage('请选择视频格式文件')
        }
      } else { // 删除
        this.formData.videoResources = [
          {
            remark: '',
            vdResourceId: '',
            url: ''
          }
        ]
      }
    },
    getProductImg(item, index) {
      // 获取商品组图
      this.$set(this.formData.imageResources, index, {
        remark: '',
        imgResourceId: item[0].id,
        url: item[0].url
      })
      this.errorMessage = {}
    },
    imgMove(index, status) {
      // 图片移动
      if (status === 'next') {
        if (index === 0) {
          // 第一张不能点
          return false
        } else {
          const tmpItems = this.formData.imageResources[index - 1]
          this.$set(
            this.formData.imageResources,
            index - 1,
            this.formData.imageResources[index]
          )
          this.$set(this.formData.imageResources, index, tmpItems)
        }
      } else {
        if (index === 4) {
          // 最后一张不能点
          return false
        } else {
          const tmpItems = this.formData.imageResources[index + 1]
          this.$set(
            this.formData.imageResources,
            index + 1,
            this.formData.imageResources[index]
          )
          this.$set(this.formData.imageResources, index, tmpItems)
        }
      }
    },
    imgDelete(index) {
      // 删除图片信息
      this.$set(this.formData.imageResources, index, {
        remark: '',
        imgResourceId: '',
        url: ''
      })
    },
    changeTemplate(id) { // 运费模板的选择切换
      freightGet({ id: id }).then(res => { // 获取运费模板详情
        this.freightInfo = res.data
      })
    }
  },
  filters: {
    deliveryWayFilter(way) { // 运费模板运送方式的处理
      const waysArr = ['', '快递', 'ems', '平邮']
      return waysArr[parseInt(way)]
    },
    districtFilter(list) { // 运费模板地区的处理
      if (list === '') {
        return ''
      } else {
        const obj = JSON.parse(list)
        const arr = []// 存储对象的属性值
        for (const val of Object.values(obj)) {
          arr.push(val)
        }
        return arr[0] + '...'// 只返回第一条（）
      }
    }
  },
  activated() {
    categoryNames = []
    if (this.$route.query.productId && this.$route.query.productId !== 0) {
      this.initData() // 初始化数据
    }
  },
  mounted() {
    const lay = document.querySelector('#layout')
    lay.addEventListener('scroll', e => {
      let scrollTop = e.target.scrollTop
      const clientHeight = e.target.clientHeight
      const scrollHeigh = e.target.scrollHeight
      if (this.fixed === false) {
        scrollTop += 70
      }
      if (parseInt(scrollTop) > scrollHeigh - clientHeight - 70) {
        this.fixed = false
      } else {
        this.fixed = true
      }
    })
  }
}
</script>
<style lang="scss" scoped>

</style>




